import './Me.css'
import { Button, Card, Col, Row} from 'antd';
import {
    GithubOutlined,
    TwitterOutlined,
    YoutubeOutlined
  } from '@ant-design/icons';
import touxiang from './touxiang.jpg'
import React, { Component } from 'react'

export default class Me extends Component {
    constructor(props){
        super(props);
        this.state={
            articalnum:0,
            classifunum:0
        }
    }
    render() {

        return (
            <div className='MeDiv'>
                <img className='Avatar' src={touxiang} alt="hc2333"/>
                <p className='Name'>HeCheng</p>
           <div className="site-card-wrapper">
                    <Row gutter={12}>
                    <Col span={12}>
                        <Card title="文章" bordered={false}>
                        {this.state.articalnum}
                        </Card>
                    </Col>
                    <Col span={12}>
                        <Card title="分类" bordered={false}>
                        {this.state.classifunum}
                        </Card>
                    </Col>
                    </Row>
             </div>
                <Button
                 type="primary" 
                 shape='round'
                 target="_blank" 
                 className='GithubButton' 
                 href="https://github.com/hc2333"
                >
                 关注我
                </Button> 
                <div className='IconLinkDiv'>
                    <a target="blank"  className='IconLink' href="https://github.com/hc2333">{<GithubOutlined  />}</a>
                    <a target="blank" className='IconLink'  href="https://twitter.com/yecgaa2333">{<TwitterOutlined />}</a>
                    <a target="blank" className='IconLink'  href="https://www.youtube.com/channel/UCXAFVoa4iOZoXF2uRgCZGIA">{<YoutubeOutlined />}</a>
                </div>
            </div>
        )
    }
}
