import React, { Component } from 'react'
import './Artical.css'
import { List} from 'antd';
import{
    CalendarOutlined,
    FolderOutlined,
    FireOutlined
} from '@ant-design/icons';
import {Link} from 'react-router-dom'
export default class Artical extends Component {
    constructor(props)
    {
        super(props);

        this.state={
            
            mylist:
            [
                {title:'第一篇',context:"第一篇博客的内容"},
                {title:'第二篇',context:"第二篇博客的内容"},
                {title:'第三篇',context:"第三篇博客的内容"},
                {title:'第四篇',context:"第四篇博客的内容"},
                {title:'第五篇',context:"第五篇博客的内容"},
                {title:'第六篇',context:"第六篇博客的内容"},
                {title:'第七篇',context:"第七篇博客的内容"},
                {title:'第八篇',context:"第八篇博客的内容"},
                {title:'第九篇',context:"第九篇博客的内容"}
            ]
        }
    }
    render() {

        var listsource=[];

        for (var i = this.state.mylist.length - 1,j=0; i >= 0; i--,j++) {
            listsource[i] = this.state.mylist[j] 
        }
        return (
            
            <div className='ArticalDiv'>
                <List
                    header={<div className='ListHeader'>最新日志</div>}
                    itemLayout='vertical'
                    dataSource={listsource}
                    renderItem={(item,column)=> (
                    <List.Item className="ListItem">
                        <div className='ListTitle'>
                            <Link 
                            to={{pathname:'/artical/'+ (listsource.length-column),
                               }        
                            }>
                                    {item.title}
                            </Link>
                        </div>
                        <div className='ListIcon'>
                            <span ><CalendarOutlined /> 2021年3月30日</span>
                            <span ><FolderOutlined /> js教程</span>
                            <span ><FireOutlined /> 999</span>

                        </div>
                        <div className='ListContext'>{item.context}</div>
                    </List.Item>
                    )}
                />
            </div>
        )
    }
}
