import './Lable.css'

import { Tag } from 'antd';
import React, { Component } from 'react'

export default class Lable extends Component {
    render() {
        return (
            <div className='LableDiv'>
                <div className='TagDiv'>
                <Tag  color="#f50" className="Tag">#HTML</Tag>
                <Tag  color="#2db7f5" className="Tag">#CSS</Tag>
                <Tag  color="#87d068" className="Tag">#React</Tag>
                <Tag  color="#108ee9" className="Tag">#C</Tag>
                </div>
            </div>
        )
    }
}
