import About from './Pages/About/About'
import Help from './Pages/Help/Help'
import Admine from './Pages/Admine/Admine'
import Detail from './Components/Detail/Detail'
import { Layout, Menu, Breadcrumb } from 'antd';
import { Switch, Route, Link,useLocation } from 'react-router-dom'
import './App.css';
const { Header, Content, Footer } = Layout;

function App() {
  const path = useLocation().pathname //获取当前路由路径
  return (
    <div className="App">
        <Layout >
          <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
            <div className="logo" />
            <Menu theme="dark" mode="horizontal" selectedKeys={[path]}>
              <Menu.Item key="/">
                  <Link to='/'>主页</Link>
              </Menu.Item>
              <Menu.Item key="/about">
                  <Link to='/about'>关于我</Link>
              </Menu.Item>
              <Menu.Item key="/help">
                  <Link to='/help'>帮助</Link>
              </Menu.Item>
            </Menu>
          </Header>

          <Content className="site-layout" style={{ padding: '0 50px', marginTop: 64 }}>
          <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item><Link to='/'>/home</Link></Breadcrumb.Item>
            </Breadcrumb>
            <div className="site-layout-background" style={{ padding: 24, minHeight: 380 }}>
                <Switch> 
                  <Route exact path='/' component={Admine}/> 
                  <Route path='/about' component={About}/>
                  <Route path='/help' component={Help}/>
                  <Route path='/artical' component={Detail}/>
                </Switch>
            </div>
          </Content>
          <Footer style={{ textAlign: 'center',color:'rgb(75, 75, 75)' }}>使用React+Antd完成</Footer>
        </Layout>
    </div>
  );
}

export default App;
