import './About.css'
import React, { Component } from 'react'
import { Descriptions } from 'antd';

export default class About extends Component {
    render() {
        return (
            <div className='AboutDiv'>

                <Descriptions
                    title="关于我"
                    bordered
                    column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
                >
                    <Descriptions.Item label="姓名" className="Descriptions">  何成</Descriptions.Item>
                    <br/><br/>
                    <Descriptions.Item label="学校" className="Descriptions">  南京工业大学</Descriptions.Item>
                    <br/><br/>
                    <Descriptions.Item label="学院" className="Descriptions">  计算机科学与技术学院</Descriptions.Item>
                    <br/><br/>
                    <Descriptions.Item label="专业" className="Descriptions">  电子信息类</Descriptions.Item>
                    <br/><br/>
                    <Descriptions.Item label="班级" className="Descriptions">  电子类2001</Descriptions.Item>
                    <br/><br/>
                    <Descriptions.Item label="前端框架" className="Descriptions">
                        js框架：React
                        <br />
                        UI框架：Antd
                        <br />
                    </Descriptions.Item>
                </Descriptions>
            </div>
        )
    }
}
